.AppShell {
  max-width: 960px;
  margin: 0 auto;
  padding: var(--space-6) var(--space-4);
  display: flex;
  flex-direction: column;
  gap: var(--space-5);
}

.AppHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--space-4);
}

.AppKicker {
  margin: 0;
  color: var(--color-muted);
  font-size: var(--font-size-2);
}

.AppTitle {
  margin: var(--space-1) 0 0;
  font-size: var(--font-size-5);
}

.AppMain {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
}

.AppFooter {
  color: var(--color-muted);
  font-size: var(--font-size-2);
}

.NavBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--space-4);
  padding: var(--space-3);
  background: var(--color-surface);
  border-radius: var(--radius-2);
  box-shadow: var(--shadow-1);
}

.NavBarLinks {
  display: flex;
  gap: var(--space-2);
  flex-wrap: wrap;
}

.NavBarLink {
  background: transparent;
  border: 1px solid transparent;
  padding: var(--space-2) var(--space-3);
  border-radius: var(--radius-1);
  color: var(--color-text);
  cursor: pointer;
}

.NavBarLink.is-active {
  border-color: var(--color-primary);
  color: var(--color-primary);
  font-weight: 600;
}

.NavBarActions {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.NavBarRole {
  color: var(--color-muted);
  font-size: var(--font-size-2);
}

.Card {
  background: var(--color-surface);
  border-radius: var(--radius-2);
  padding: var(--space-4);
  box-shadow: var(--shadow-1);
}

.CardTitle {
  margin: 0 0 var(--space-3);
  font-size: var(--font-size-4);
}

.CalendarHeader {
  display: flex;
  flex-direction: column;
  gap: var(--space-2);
  margin-bottom: var(--space-3);
}

.CalendarPagination {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  flex-wrap: wrap;
}

.CalendarMonth {
  font-weight: 600;
  text-transform: capitalize;
}

.ButtonRow {
  display: flex;
  gap: var(--space-2);
  flex-wrap: wrap;
  margin-top: var(--space-3);
}

.Button {
  border: none;
  border-radius: var(--radius-1);
  padding: var(--space-2) var(--space-3);
  cursor: pointer;
}

.Button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.Button--primary {
  background: var(--color-primary);
  color: var(--color-primary-contrast);
}

.Button--secondary {
  background: var(--color-surface);
  color: var(--color-text);
  border: 1px solid var(--color-border);
}

.Button--ghost {
  background: transparent;
  color: var(--color-primary);
  border: 1px solid transparent;
}

.Badge {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: var(--radius-1);
  font-size: var(--font-size-1);
  font-weight: 600;
}

.Badge--primary {
  background: rgba(37, 99, 235, 0.15);
  color: var(--color-primary);
}

.Badge--muted {
  background: var(--color-border);
  color: var(--color-muted);
}

.Badge--success {
  background: rgba(22, 163, 74, 0.15);
  color: var(--color-success);
}

.Badge--warning {
  background: rgba(245, 158, 11, 0.15);
  color: var(--color-warning);
}

.Badge--danger {
  background: rgba(220, 38, 38, 0.15);
  color: var(--color-danger);
}

.BadgeRow {
  display: flex;
  gap: var(--space-1);
  flex-wrap: wrap;
}

.SegmentCell {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.SegmentTime {
  font-size: var(--font-size-2);
  color: var(--color-muted);
}

.LegendItem {
  display: inline-flex;
}

.TableWrapper {
  width: 100%;
  overflow-x: auto;
}

.Table {
  width: 100%;
  border-collapse: collapse;
  font-size: var(--font-size-2);
}

.Table th,
.Table td {
  text-align: left;
  padding: var(--space-2);
  border-bottom: 1px solid var(--color-border);
}

.Table--editable th:not(:last-child),
.Table--editable td:not(:last-child) {
  min-width: 10rem;
}

.TableEmpty {
  color: var(--color-muted);
}

.FormField {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.FormFieldLabel {
  font-weight: 600;
}

.FormFieldHint {
  color: var(--color-muted);
  font-size: var(--font-size-1);
}

.AuthForm {
  display: grid;
  gap: var(--space-3);
  max-width: 360px;
  margin-top: var(--space-3);
}

.Input {
  padding: var(--space-2) var(--space-3);
  border-radius: var(--radius-1);
  border: 1px solid var(--color-border);
}

.Input:focus {
  outline: 2px solid rgba(37, 99, 235, 0.25);
  border-color: var(--color-primary);
}

.StatGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: var(--space-3);
}

.Stat {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
  padding: var(--space-3);
  border-radius: var(--radius-1);
  border: 1px solid var(--color-border);
}

.StatLabel {
  color: var(--color-muted);
  font-size: var(--font-size-2);
}

.StatValue {
  font-size: var(--font-size-4);
  font-weight: 600;
}

.PageStack {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
}

.ResumeHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--space-3);
}

.ResumeTitle {
  margin: 0;
  font-size: var(--font-size-4);
}

.SchoolYearDisplay {
  padding: var(--space-2) var(--space-3);
  font-size: var(--font-size-3);
  color: var(--color-text);
  background-color: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-2);
}

.Muted {
  color: var(--color-muted);
}

.ErrorList {
  margin: 0;
  padding-left: var(--space-4);
  color: var(--color-danger);
}

.ErrorText {
  margin: 0;
  color: var(--color-danger);
  font-size: var(--font-size-2);
}

.TableSeparator {
  padding: var(--space-3) var(--space-2) !important;
  background-color: var(--color-border);
  font-weight: 600;
  text-align: center;
  color: var(--color-text);
}
